import React from 'react'
import Slider from "react-slick";
function Testimonials({ items }) {

    const sliderConfig = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 3,
        autoplay: true,
        arrows: false,
        responsive: [
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,

                }
            },
        ]
    }

    return (
        <div className="testimonials">

            <h2 className="heading">
                What Customers Think About Us</h2>
            <p className="heading">our customer reviews</p>
            <div className="w3_testimonials_grids testimonials-clean">
                <div className="flex-viewport row people" >
                    <Slider {...sliderConfig} >
                        {
                            items.map((item, key) =>


                                <>
                                    <div class="testimonial-wrap" key={key}>
                                        <article dangerouslySetInnerHTML={{ __html: item.details}}>
                                             </article>

                                    </div>
                                    <div className="author">
                        <h5 >{item.title}</h5>
                                    </div>
                                    
                                </>




                            )
                        }
                    </Slider>
                </div>
            </div>

        </div>






    )
}

Testimonials.defaultProps = {
    items: []
}

export default Testimonials;

