export const getBannerImg = (apiValues) =>{
   const {imagesList=[]}= apiValues
   return imagesList.filter((imgs)=>{
       return imgs.type === "Banner"
   })
}

export const getWhatWeServe = (apiValues) => {
    const {whatWeServe=[]}= apiValues
    const arrayOneToTen = whatWeServe.slice(0, 10);
    const arrayTenToTwnty = whatWeServe.slice(10, 20);
    return {arrayOneToTen,arrayTenToTwnty}
}
export const getGlryImg = (apiValues) =>{
    const {imagesList=[]}= apiValues
    return imagesList.filter((imgs)=>{
        return imgs.type === "Gallery"
    })
 }

 export const getTestimonals = (apiValues) =>{
    const {testimonials=[]}= apiValues
    return testimonials || []
 }

 export const getCategory = (apiValues) =>{
    const {category=[]}= apiValues
    
    return category
 }
 export const getListByCategoryTitle = (apiValues,titles) =>{
    const {all_blogs=[]}= apiValues
    
    return all_blogs.filter((itm)=>
        itm.cat===titles
    )
 }