import React from 'react'
import { Button } from '@material-ui/core';

import Typography from "@material-ui/core/Typography";
import Card from '@material-ui/core/Card';
import { makeStyles } from '@material-ui/core/styles';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import config from '../../../../constants/config'
const IMG_URL = config.IMG_URL
function HomeGallery({ items }) {
	const useStyles = makeStyles({
		root: {
			backgroundColor: "transparent",
			marginBottom: 20
		},
		media: {
			height: 200,
		},
		cardContent: {
			background: "transparent",
			padding: "10px 10px",
			//border: "1px solid #808080b0"
		}
	});
	const classes = useStyles();
	return (
		<div class="specials">

			<h2 class="heading">Our <span> Gallery</span></h2>
			<p class="heading">Our special Gallery</p>
			<div class="special-grids row">
				{
					items.map((itm, index) =>
						<div className="col-lg-3 col-md-3 col-sm-6 galleryHower" key={index}>
							<Card className={classes.root}>
								<CardMedia
									className={classes.media}
									image={`${IMG_URL}${itm.image}`}
									title="Contemplative Reptile"
								/>
								<CardContent className={classes.cardContent}>
									<Typography align="center" variant="h6" component="p">
										{itm.title}
									</Typography>
									<Typography variant="body2" align="center" color="textSecondary" component="p">
										<div dangerouslySetInnerHTML={{ __html: itm.details }}></div>  </Typography>
									<div className="card-footer-button">
										<Button variant="outlined" color="secondary" href={itm.url}>
											View More
				</Button>
									</div>

								</CardContent>

							</Card>
						</div>
					)
				}


			</div>
		</div>
	)
}



export default HomeGallery;

