import React, { useState, useEffect } from 'react';
import Layout from '../../element/Layout'
import Slides from '../../element/Sliders/Slides/Slides'
import { BannerSlider } from '../../element/Sliders'
import Skeleton from '@material-ui/lab/Skeleton';
import HomeGallery from './HomeGallery'
import Testimonials from '../../element/Testimonials'
import BadgeSlider from './BadgeSlider'
import { getBannerImg, getWhatWeServe, getGlryImg, getTestimonals, getCategory, getListByCategoryTitle } from './homeUtils'
import { getDaynamicGetData } from '../../../services/services'
function Home() {
  const [homePageData, setHomePageData] = useState({})
  const [loding, setLoading] = useState(true)
  useEffect(() => {
    getDaynamicGetData('getHomePageInfo')
      .then(setHomePageData)
      .then(setLoading(false))
  }, []);


  const badges = getWhatWeServe(homePageData)

  return (
    <>
      {loding ? <>
        <Skeleton variant="rect" width="100%" height="50vh" />
        <Skeleton variant="rect" width="25%" height="20px" style={{ marginTop: 20 }} />
        <Skeleton variant="rect" width="100%" height="30vh" style={{ marginTop: 10 }} />
        <Skeleton variant="rect" width="25%" height="20px" style={{ marginTop: 20 }} />
        <Skeleton variant="rect" width="100%" height="30vh" style={{ marginTop: 10 }} />
      </>
        :
        <Layout settings={homePageData?.settings?.[0] || {}} >
          <BannerSlider items={getBannerImg(homePageData)} />
          <div className="badgeWrapper">
            <h5 className="MuiTypography-root MuiTypography-h5 MuiTypography-alignLeft" > What We Serve </h5>
            <BadgeSlider items={badges?.arrayOneToTen || []} />
            <BadgeSlider items={badges?.arrayTenToTwnty || []} />

          </div>

          {
            getCategory(homePageData)?.map((itm, index) =>
              <Slides key={index} items={getListByCategoryTitle(homePageData, itm.title)} isSmall={itm.viewType == 2} allLink={itm.url} title={itm.title} />
            )
          }
          <HomeGallery items={getGlryImg(homePageData)} />
          <Testimonials items={getTestimonals(homePageData)} />

        </Layout>

      }
    </>








  );
}
export default Home
